//REACT
import React, { useState } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';

//COMPONENTS
import Navigation from './Navigation';
import Login from "../Account/Login";
import Account from "../Account/Account";
import Internal from "../Internal/Internal";
import External from "../External/External";

//CONTEXT
import InternalContext from '../Internal/InternalContext';
import ExternalContext from '../External/ExternalContext';

//AUTH
import { AuthenticatedRoute, AuthProvider, UnauthenticatedRoute } from "../../authentication/Auth";

function App() {
    const [internalFunctionality, setInternalFunctionality] = useState('internalHome');
    const [externalFunctionality, setExternalFunctionality] = useState('externalHome');
    
    return (
        <AuthProvider>
            <InternalContext.Provider value={{ internalFunctionality, setInternalFunctionality }}>
                <ExternalContext.Provider value={{ externalFunctionality, setExternalFunctionality }}>
                    <Router>
                        <div className="App">
                            <header className="App-header">
                                <Navigation />
                            </header>
                        </div>
                        <Routes>
                            <Route exact path='/' element={<AuthenticatedRoute />}>
                                <Route exact path='/' element={<Internal />} />
                            </Route>

                            <Route exact path='/account' element={<AuthenticatedRoute />}>
                                <Route exact path='/account' element={<Account />} />
                            </Route>

                            <Route exact path='/login' element={<UnauthenticatedRoute />}>
                                <Route path='/login' element={<Login />} />
                            </Route>
                            
                            <Route exact path='/external' element={<External />} />
                        </Routes>
                    </Router>
                </ExternalContext.Provider>
            </InternalContext.Provider>
        </AuthProvider>
    );
}

export default App;
